body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-width: 320px;
}

input, textarea {
  outline: none;
  border: 1px solid #e6e6e6;
  padding: 5px;
  border-radius: 3px;
}

textarea {
  resize: none;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
  padding: 5px 10px;
  background: #00a3cf;
  color: #FFF;
  border-radius: 3px;
}

p {
  margin: 0;
}

.wrapper {
  max-width: 500px;
  margin: 10px auto;
  font-size: 14px;
  background-color: #fff;
  border: 1px solid #f1f1f1;
  padding: 1em;
}

.comment-input {
  margin-bottom: 1em;
  padding: 1em;
  border: 1px solid #f1f1f1;
}

.comment-field {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em; 
}

.comment-field-name {
    -ms-flex-preferred-size: 21%;
        flex-basis: 21%;
}

@media screen and (max-width: 400px) {
      .comment-field {
          -ms-flex-direction: column;
              flex-direction: column;
      }
      .comment-field-name {
         margin-bottom: 1em;
      }
}

.comment-field-input {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    display: -ms-flexbox;
    display: flex;
}

.comment-field-input  input, .comment-field-input  textarea {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.comment-field-input  textarea {
    height: 100px;
}

.comment-field-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.comment-field-button button {
    min-width: 16%;
}

.comment {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #CCC;
  margin-bottom: 1em;
  padding: 1em;
  position: relative;
}

.comment .comment-user {
  color: #00a3cf;
  font-weight: bold;
}

/*.comment-user {
  flex-shrink: 0
}*/

.comment-createdtime {
  padding-right: 5px;
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 5px;
  font-size: 12px;
}

.comment:hover .comment-delete {
  color: #00a3cf;
}

.comment-delete {
  position: absolute;
  right: 0;
  top: 0;
  color: transparent;
  font-size: 12px;
  cursor: pointer;
}

.comment p {
  margin: 0;
}

code {
  border: 1px solid #ccc;
  background: #f9f9f9;
  padding: 0px 2px;
}


